body {
  margin: 0;
  font-family:Arial, Helvetica, sans-serif;
}

.App {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
}

.form-container {
  background-color: whitesmoke;
  width: 45%;
  display: flex;
  flex-direction: column;
}

.updated-form-container {
  background-color: whitesmoke;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

}

.checkbox-container {
  display: flex;
  justify-content: space-between;
  width: 200px;
}

h3 {
  text-align: center;
}

h4 {
  margin: 0;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 70%;
  margin: 20px auto;
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.412);
  border-radius: 8px;
  padding: 10px;
}

textarea {
  width: 100%;
  height: 100px;
  padding: 10px;
  margin-top: 8px;
  box-sizing: border-box;
  border: 2px #ccc;
  border-radius: 5px;
  background-color: whitesmoke;
  font-size: 16px;
  font-family: inherit;
  outline: none;
}

p {
  margin: 8px 0;
  color: black;
  font-size: 18px;
}

.sub-heading {
  color: rgb(156, 156, 156);
}

label {
  font-size: 18px;
}

input {
  background-color: whitesmoke;
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 5px;
  font-size: 16px;
}

.upload-area {
  display: flex;
  justify-content: space-between;
  
}

.upload-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background: whitesmoke;
  color: red;
  width: 50%;
  padding: 5px;
  margin: 10px;
  height: 150px;
  border-radius: 8px;
}

/* .upload-btn {
  background: transparent;
  border: none;
  color: red;
} */

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.submit {
  background-color: red;
  color: white;
  padding: 10px 15px;
  border-radius: 8px;
  border: none;
  width: 100px;
  margin-bottom: 20px;
}

button {
  cursor: pointer;
  
}

.main-claims-container {
  height: 100vh;
  width: 45%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-around; */
      
}

.claims-container {
  /* border: 1px solid grey; */
  border-radius: 8px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.claim {
  margin: 15px auto;
  border: 1px solid rgba(0, 0, 0, 0.412);
  border-radius: 8px;
  padding: 10px;
  width: 60%;
}

.modal {
  z-index: 5;
  position: fixed;
  top: 50%;
  left: 50%;
  background: whitesmoke;
  width: 600px;
  height: 500px;
  border: 1px solid black;
  transform: translate(-50%, -50%);
}

.claim-details {
  padding: 5px 10px;
}

.update-modal {
  z-index: 5;
  position: fixed;
  overflow-y: auto;
  top: 50%;
  left: 50%;
  background: whitesmoke;
  width: 600px;
  height: 500px;
  border: 1px solid black;
  transform: translate(-50%, -50%);
}

.map-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  background: whitesmoke;
  width: 75%;
  height: 75%;
  border: 1px solid black;
  transform: translate(-50%, -50%);
  padding: 10px;
}

.map-btn {
  position: absolute;
  z-index: 10;
  background-color: red;
  color: white;
  padding: 3px 8px;
  border-radius: 5px;
  border: none;

}

.marker {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: red;
  border: 1px solid black;
}

.btn-container {
  width: 400px;
  display: flex;
  justify-content: space-between;
  padding-left: 10px;
}

.claim-btn {
  background-color: rgba(255, 255, 255, 0.758);
  color: black;
  padding: 10px 15px;
  border-radius: 8px;
  border: 1px solid black;
}

.close-btn {
  background-color: red;
  color: white;
  padding: 3px 8px;
  border-radius: 5px;
  border: none;
  margin: 10px;
}